import { Cart, NegotiableQuote } from '@/checkout/data/models';
import { events } from '@adobe-commerce/event-bus';

type DataModel = Cart | NegotiableQuote;

export function getInitialCheckoutData(): DataModel | null {
  return events.lastPayload('checkout/initialized') ?? null;
}

export function getLatestCheckoutUpdate(): DataModel | null {
  return events.lastPayload('checkout/updated') ?? null;
}

export function getCheckoutData(): DataModel | null {
return getLatestCheckoutUpdate() ?? getInitialCheckoutData();
}

export function hasShippingAddress(): boolean {
  const cart = getCheckoutData();
  return !!cart?.shippingAddresses?.length;
}

export function getCartEmail(): string | null {
  const cart = getCheckoutData();
  return cart?.email ?? null;
}
